<template>
<div class="side-left">
    <transition name="slide-fade">
        <div class="menu" v-if="show">
            <div class="logo"><img src="../../static/img/logo.png" width="100" height="100"/></div>
            <div class="logo-name">进击の菜鸟</div>
            <div class="endTime">
                <div class="txt">七七生日倒计时</div>
                <endTime endTime="1561683600" :callback="callback" endText="生日已过~"></endTime>
            </div>
            <ul class="menu-ul">
                <li class="menu-li" v-for="l in list">{{l.name}}</li>
            </ul>
            <div class="progress"><span style="width: 50%;"><span>50%</span></span></div>
        </div>
    </transition>
    <div class="side-btn-left" @click="show = !show"><</div>
</div>
</template>

<script>
import '@/../static/css/progress.css'
import endTime from '@/page/endTime'
    export default{
        name:"side-left",
        data () {
            return {
                list: [],
                show: true
            }
        },
        beforeMount() {
            var self = this;
            getObject();
            function getObject() {
                self.axios.get('/api/mydata').then(function(res){
                    console.log(res.data)
                    self.list = res.data.data.list;
                }).catch(function(error){
                    console.log(error);
                })
            }
            this.$nextTick(function() {
                $(".side-btn-left").on("click", function() {
                    if($(this).hasClass("non-mgl")){
                        $(this).removeClass("non-mgl");
                        $(".mainT").removeClass("non-mgl");
                    } else {
                        $(this).addClass("non-mgl");
                        $(".mainT").addClass("non-mgl");
                    }
                })
            })
        },
        components: {
            endTime
        }
    }
</script>
<style>
.slide-fade-enter-active, .side-btn-left, .mainT {
    transition: all .5s ease;
}
.slide-fade-leave-active, .non-mgl {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(-252px);
    opacity: 0;
}
.non-mgl {
    margin-left:0 !important;
}
.side-left {
    width: 252px;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
}
.side-btn-left {
    margin-left: 220px;
    margin-top: 100%;
    width: 30px;
    height: 100px;
    background: rgba(204, 234, 255, 0.8);
    border-radius: 0 10px 30px 0;
    text-align: center;
    line-height: 85px;
    color: #fff;
    font-size: 38px;
    cursor: pointer;
}
.menu {
    width: 220px;
    height: 100%;
    border-right: 1px dashed #d6e6ff;
    background-color: #cceaff;
    text-align: center;
    float: left;
}
.logo img {
    border-radius: 50px;
    overflow: hidden;
    border: 2px solid #fff;
    margin-top: 20px;
    transition: All 1.2s ease-in-out;
    -webkit-transition: All 1.2s ease-in-out;
    -moz-transition: All 1.2s ease-in-out;
    -o-transition: All 1.2s ease-in-out;
}
.logo img:hover {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
}
.logo-name {
    font-size: 26px;
    text-shadow: 0 0 5px #1B00FF;
    color: white;
    font-weight: bold;
    padding: 10px 0 15px 0;
    border-bottom: 1px dashed #fff;
}
.endTime {
    padding-top: 20px;
    font-family: "微软雅黑";
    color: #7A00FF; 
}
.endTime .txt {
    font-size: 12px;
}
.menu-ul {
    margin-left: -40px;
    margin-top: 10px;
}
.menu-li {
    color: black;
    font-family: "微软雅黑";
    padding: 0 30px;
    height: 30px;
    line-height: 30px;
    overflow: hidden;
    cursor: pointer;
    transition: All 0.4s ease-in-out;
    -webkit-transition: All 0.4s ease-in-out;
    -moz-transition: All 0.4s ease-in-out;
    -o-transition: All 0.4s ease-in-out;
}
.menu-li:hover {
    transform: scale(1.2);
    -webkit-transform: scale(1.2);
    -moz-transform: scale(1.2);
    -o-transform: scale(1.2);
    -ms-transform: scale(1.2);
}
</style>


