<template>
    <div class="mainT">
        <div class="des">
            <label>time-Example</label><br/>
            <div id="countdown">
                <div id='tiles'></div>
            </div>
        </div>
        <ul class="side-ul">
            <li class="side-li" v-for="l in list">
                <div class="name">{{l.name}}</div>
                <div class="des" v-if="l.description">{{l.description}}</div>
                <ul class="des" v-if="l.features">
                    <label>特点</label>
                    <li v-for="feature in l.features">{{feature}}</li>
                </ul>
                <ul class="des" v-if="l.leadIns">
                    <label>引入方式 —— 使用淘宝镜像</label>
                    <li v-for="leadIn in l.leadIns">{{leadIn}}</li>
                </ul>
                <ul class="des">
                    <label>配置</label>
                    <li v-for="configure in l.configures">{{configure}}</li>
                </ul>
                <div class="des" v-if="l.imgUrl"><img :src=l.imgUrl width="700" height="550"></div>
            </li>
        </ul>
        <div class="des">
            <label>multiple-Example</label><br/>
            <div class="shadow">
                <div id="load-wrap">
                    <div id="load">
                        <span class="item-1"></span>
                        <span class="item-2"></span>
                        <span class="item-3"></span>
                        <span class="item-4"></span>
                        <span class="item-5"></span>
                        <span class="item-6"></span>
                        <span class="item-7"></span>
                    </div>
                </div>
            </div>
            <div class="svg-wrap">
                <dtsvg></dtsvg>
                <div class="icon" id="J-sd-demo">
                    <em></em>
                    <div class="txt">click</div>
                    <div class="alt" data-type="alert">点我试试看！</div>
                    <script id="J-alert" type="text/html">
                        <div id="haha" class="window">
                            <div class="window-ani-lt"></div>
                            <div class="window-ani-rt"></div>
                            <div class="item">
                                <h1>ZZZ</h1>
                                <div>
                                    <p><span>XXX</span></p>
                                    <p>ddd：135</p>
                                    <p>ddd：246</p>
                                    <p>ddd：357</p>
                                </div>
                                <div>
                                    <p><span>YYY</span></p>
                                    <p>aaa：135</p>
                                    <p>ttt：79</p>
                                    <p>uuu：58</p>
                                </div>
                            </div>
                        </div>
                    </script>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import '@/../static/css/load.css'
import '@/../static/css/icon.css'
import '@/../static/css/slideAlert.css'
import '@/../static/js/slideAlert.js'
import '@/../static/css/datetime.css'
import '@/../static/js/datetime.js'
import dtsvg from '@/svg/dtsvg'
    export default{
        name:"mainT",
        data () {
            return {
                list: []
            }
        },
        beforeMount() {
            var self = this;
            getObject();
            function getObject() {
                self.axios.get('/api/mydata').then(function(res){
                    console.log(res.data)
                    self.list = res.data.data.list;
                }).catch(function(error){
                    console.log(error);
                })
            }
            this.$nextTick(function() {
                $(".icon .txt").mouseover(function() {
                    $(".icon .alt").show();
                })
                $(".icon .txt").mouseout(function() {
                    $(".icon .alt").hide();
                })
            })
        },
        mounted: function () {
            var self = this;
            var n1_lt = $(".window-ani-lt").width(),
                h1_lt = $(".window-ani-lt").height(),
                divTop_lt ="<div class='window-bef divTop_lt'></div>",
                divRight_lt ="<div class='window-bef divRight_lt'></div>",
                divBottom_lt ="<div class='window-bef divBottom_lt'></div>",
                divLeft_lt ="<div class='window-bef divLeft_lt'></div>"; 
            var n1_rt = $(".window-ani-rt").width(),
                h1_rt = $(".window-ani-rt").height(),
                divTop_rt ="<div class='window-bef divTop_rt'></div>",
                divRight_rt ="<div class='window-bef divRight_rt'></div>",
                divBottom_rt ="<div class='window-bef divBottom_rt'></div>",
                divLeft_rt ="<div class='window-bef divLeft_rt'></div>"; 
            this.$nextTick(function () {
                $(".icon .txt").on("click",function(){
                    $(".shadow").show();
                    $("#load-wrap").show();
                    setTimeout(showInfo,1000);
                })
                function showInfo() {
                    $(".shadow").hide();
                    $("#load-wrap").hide();
                    slideAlert();
                }
                function slideAlert() {
                    var type = $('.alt').attr('data-type');
                    var instance = $('.alt').slideAlert({
                        type: type,
                        content: $('#J-' + type).html(),
                        beforeOpen: function () {
                           console.log('before open callback');
                        },
                        afterClose: function () {
                           console.log('after close callback');
                        },
                        afterOpen: function () {
                            window_ani();
                        }
                    });
                };
                function window_ani(){
                    $(".window-ani-lt").append(divTop_lt,divRight_lt,divBottom_lt,divLeft_lt);
                    $(".window-ani-lt").find("div:nth-child(odd)").stop().animate({width:150},1000);
                    $(".window-ani-lt").find("div:nth-child(even)").stop().animate({height:80},1000);

                    $(".window-ani-rt").append(divTop_rt,divRight_rt,divBottom_rt,divLeft_rt);
                    $(".window-ani-rt").find("div:nth-child(odd)").stop().animate({width:150},1000);
                    $(".window-ani-rt").find("div:nth-child(even)").stop().animate({height:80},1000);

                    setTimeout(function(){
                        window_ani_after();
                    },1000);
                }
                function window_ani_after(){
                    $(".window-ani-lt").find("div:nth-child(odd)").stop().animate({width:60},1000);
                    $(".window-ani-lt").find("div:nth-child(even)").stop().animate({height:100},1000);

                    $(".window-ani-rt").find("div:nth-child(odd)").stop().animate({width:60},1000);
                    $(".window-ani-rt").find("div:nth-child(even)").stop().animate({height:100},1000);
                }
            })
        },
        components: {
            dtsvg,
        },
    }
</script>
<style>
body {
    padding: 0;
    margin: 0;
}
.mainT {
    padding: 8px;
    margin-left: 232px;
}
.side-ul {
    margin-left: -40px;
    margin-top: 10px;
}
.side-li {
    list-style: none;
    text-align: left;
}
.name {
    font-size: 24px;
    font-weight: bold;
    color: #0095FF;
    line-height: 35px;
    text-indent: 20px;
}
.des {
    line-height: 30px;
    width: 96%;
    height: auto;
    padding: 10px 2%;
    margin: 10px auto;
    background-color: whitesmoke;
    font-size: 14px;
    font-family: "微软雅黑";
}
.des li {
    margin-left: 20px;
}
label {
    color: #45B1FD;
    font-size: 16px;
    font-weight: bold;
}
.wh_container {
    margin: 10px 0;
}
.wh_content_all {
    background-color: #73dcb3 !important;
}
.wh_content_item .wh_isToday {
    background: #7cafb5 !important;
}
</style>



